
import Vue from 'vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { submitImgActivityContent } from '@/api/detail'
import { uploadWechatFile } from '@/api/other.js'
export default {
name: 'shoppingCard',
components: {
Radio,
Group,
},
data () {
return {
userinput:'',
ejectstatus:false,
picslist:[],
localIds: [], // 微信图片id
serverId: [],
picArr: [],
picArrList: [],
id:'',
choose:true
}
},
created () {
this.id = this.$route.query.id
},
methods: {
goall(){
this.$router.push({path:"/showphoto/allworks",query:{"id":this.id}})
},
getupload(){
var listpic = this.picslist.join(',')
const postData = {
id:this.id,
content:this.userinput,
pics:listpic,
}
submitImgActivityContent(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.$vux.loading.hide()
this.ejectstatus = true
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
chooseImg () {
const that = this
this.$wechat.chooseImage({
count: 5 - that.picArr.length, // 默认9

sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: function (res) {
that.picArr = that.picArr.concat(res.localIds)
that.localIds = that.picArr
if(that.picArr.length==5){
that.choose = false
}
}
})
},

submitbtn(){
if(this.userinput==''){
this.$vux.toast.text('请输入您想发表的心情', 'middle')
return
}
if(this.picArr.length==0){
this.$vux.toast.text('请选择需要发布的图片', 'middle')
return
}
this.syncUpload()
},
syncUpload () {
this.$vux.loading.show()
if (!this.localIds.length) {


this.syncSubmit()
} else {
const that = this
const localId = this.localIds.pop()
that.$wechat.uploadImage({
localId: localId,
isShowProgressTips: 0,
success: function (res) {
that.serverId.push(res.serverId)
that.syncUpload()
}
})
}
},

syncSubmit () {

if (!this.serverId.length) {


this.getupload()
} else {
const that = this
const serverId = this.serverId.pop()
that.handleUpload(serverId)
}
},

handleUpload (val) {
const postData = {
mediaid: val
}
uploadWechatFile(postData)
.then(res => {
if (res.data.errorcode === 0) {


this.picslist.push(res.data.data.filePath)

this.syncSubmit()

} else {
this.$vux.loading.hide()

this.$vux.toast.text('图片上传失败', 'middle')
return false
}
})
.catch(err => {
console.log(err)
this.$vux.loading.hide()

this.$vux.toast.text('图片上传失败', 'middle')
return false
})
},

handleDelImg (index) {
this.picArr.splice(index, 1)
this.localIds = this.picArr
this.choose = true
},
previewset (index) {
const that= this
that.$wechat.previewImage({
current: that.picArr[index], // 当前显示图片的http链接
urls: that.picArr 
});
},
},
mounted(){
const that = this
Vue.wechat.ready(() => {
Vue.wechat.checkJsApi({
jsApiList: ['chooseImage', 'previewImage', 'uploadImage', 'downloadImage', 'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'scanQRCode'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function (res) {
console.log(res)
}
})

const shareid = localStorage.getItem('shareid')

Vue.wechat.onMenuShareTimeline({
title: "晒图征集活动",
link: process.env.BASE_SELF+'/weixin/index.html?#/showphoto/allworks?id='+that.id+'',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})

Vue.wechat.onMenuShareAppMessage({
title: "晒图征集活动",
desc: "",
link: process.env.BASE_SELF+'/weixin/index.html?#/showphoto/allworks?id='+that.id+'',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})
})
}
}



